<template>
  <div style="padding:20px;">
    <div style="width: 900px;">
      <Form :label-width="80" label-position="left" ref="applyForm" :model="applyForm">
        <Row :gutter="20">
          <Col span="16">
          <FormItem label="申请时间" prop="date">
            <DatePicker type="daterange" :options="dateOption" split-panels placeholder="选择时间" v-model="applyForm.date"
              style="width: 100%"></DatePicker>
          </FormItem>
          </Col>
          <Col span="8">
          <FormItem label="邀请码" prop="invitecode">
            <Input placeholder="请输入申请人邀请码" v-model.trim="applyForm.invitecode"></Input>
          </FormItem>
          </Col>
        </Row>
        <Row :gutter="20">
          <Col span="16">
          <FormItem label="申请编号" prop="keyword">
            <Input placeholder="请输入申请编号" v-model.trim="applyForm.keyword"></Input>
          </FormItem>
          </Col>
          <Col span="8">
          <FormItem label="申请状态" prop="state">
            <Select placeholder='请选择状态' v-model="applyForm.state">
              <Option v-for="v,k in stateObj" :value='k' :key="k">{{v}}</Option>
            </Select>
          </FormItem>
          </Col>
        </Row>
        <FormItem>
          <Button type="primary" style='width:100px;margin-right:20px;' @click="getData">查询</Button>
          <Button type="ghost" style='width:100px;' @click="handleReset('applyForm')">重置</Button>
        </FormItem>
      </Form>
    </div>
    <Table :loading="loading" :columns="tableColumns" :data="tableData" border class='main-table'></Table>
    <div style="margin-top: 15px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalcount" :page-size="applyForm.pagesize" :page-size-opts="[10,20,30,40]" show-elevator
          show-sizer placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'></Page>
      </div>
    </div>
    <Modal title="确认提示" v-model="showConfirm" width="500">
      <div v-if="isConfirm">
          请确认已经将款项打入对方账号成功，再点击确定！
      </div>
      <div v-else>
        <Form label-position="left" :label-width="80">
          <FormItem label="审核结果" prop="date">
            <Select placeholder='请选择状态' v-model="updateForm.state">
              <Option :value='1'>通过</Option>
              <Option :value='2'>不通过</Option>
            </Select>
          </FormItem>
          <!-- <FormItem label="备注" v-show="updateForm.state==2">
            <Input v-model="updateForm.remark"  placeholder="备注"/>
          </FormItem> -->
        </Form>
      </div>
      <div slot="footer">
          <Button @click="showConfirm=false">取消</Button>
          <Button type="primary" @click="updateRow">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
const getTimestamp = date => {
  if (date) {
    let year = date.getFullYear();
    let month = date.getMonth() + 1;
    let day = date.getDate();
    let dateStr = year + "/" + month + "/" + day;
    return Date.parse(dateStr) / 1000;
  } else {
    return "";
  }
};
export default {
  data() {
    return {
      loading: false,
      stateObj: {
        "-1": "全部",
        "0": "未审核",
        "1": "审核通过",
        "2": "审核不通过"
      },
      banktypeObj: {
        "1": "工商",
        "2": "农业",
        "3": "中国",
        "4": "建设"
      },
      dateOption: {
        shortcuts: [
          {
            text: "一周",
            value() {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              return [start, end];
            }
          },
          {
            text: "1个月",
            value() {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              return [start, end];
            }
          },
          {
            text: "3个月",
            value() {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              return [start, end];
            }
          }
        ],
        disabledDate(date) {
          return date && date.valueOf() > Date.now();
        }
      },
      applyForm: {
        date: [],
        invitecode: "",
        keyword: "",
        state: "-1"
      },
      page: {
        pagesize: 10,
        pageindex: 0
      },
      tableColumns: [
        {
          type: "expand",
          width: 50,
          render: (h, params) => {
            return h("div", [
              h(
                "span",
                { style: { marginRight: "50px" } },
                "收款人姓名:" + params.row.realname
              ),
              h(
                "span",
                { style: { marginRight: "50px" } },
                "收款人银行:" + this.banktypeObj[params.row.banktype]
              ),
              h(
                "span",
                { style: { marginRight: "50px" } },
                "收款人卡号:" + params.row.bankcard
              ),
              h(
                "span",
                { style: { marginRight: "50px" } },
                params.row.servicecharge
                  ? "手续费:" + params.row.servicecharge
                  : ""
              )
              // h('div',params.row.remark? '备注:'+params.row.remark:''),
            ]);
          }
        },
        {
          title: "#",
          width: 60,
          align: "center",
          render: (h, params) => {
            return params.index + this.page.pageindex * this.page.pagesize + 1;
          }
        },
        {
          title: "申请编号",
          key: "turnover",
          minWidth: 180,
          align: "center"
        },
        {
          title: "申请人",
          minWidth: 140,
          key: "realname",
          align: "center",
          
        },
        {
          title: "申请金额",
          key: "money",
          minWidth: 100,
          align: "center"
        },
        {
          title: "申请状态",
          minWidth: 100,
          align: "center",
          render: (h, params) => {
            return this.stateObj[params.row.state];
          }
        },
        {
          title: "申请时间",
          key: "createtime",
          minWidth: 180,
          align: "center"
        },
        {
          title: "操作",
          key: "action",
          minWidth: 220,
          align: "center",
          render: (h, params) => {
            let applyBtn = "";
            if (params.row.state == 0) {
              applyBtn = h(
                "Button",
                {
                  props: {
                    type: "primary"
                  },
                  on: {
                    click: () => {
                      this.updateForm = {
                        id: params.row.id,
                        state: 1,
                        // remark: ""
                      };
                      this.showConfirm = true;
                      this.isConfirm = false;
                    }
                  }
                },
                "审核"
              );
            }
            let lookBtn = h(
              "Button",
              {
                style: {
                  marginLeft: "5px"
                },
                on: {
                  click: () => {
                    this.$router.push({
                      path: "/member/detail",
                      query: { id: params.row.memid }
                    });
                  }
                }
              },
              "申请人详情"
            );
            return h("div", [applyBtn, lookBtn]);
          }
        }
      ],
      tableData: [],
      totalcount: 0,
      showConfirm: false,
      isConfirm: false, //是否确认条例
      updateForm: {
        id: "",
        state: 1,
        // remark: ""
      }
    };
  },
  watch: {
    page: {
      handler(newval) {
        this.getData();
      },
      deep: true
    }
  },
  mounted: function() {
    this.$sitemap.prvname = "资金管理";
    this.$sitemap.currname = "申请提现列表";
    this.$menuactive.open = ["AssetsBlock"];
    this.$menuactive.active = "ApplyReimburse";
    this.getData();
  },
  methods: {
    //重置
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    //格式化form
    formatForm() {
      let applyForm = {};
      let that = this;
      Object.keys(that.applyForm).forEach(key => {
        switch (key) {
          case "date":
            if (
              that.applyForm[key][1] !== "" &&
              that.applyForm[key][1] !== ""
            ) {
              applyForm.starttime = getTimestamp(that.applyForm[key][0]);
              applyForm.endtime = getTimestamp(that.applyForm[key][1]) + 86400;
            }
            break;
          default:
            applyForm[key] = that.applyForm[key];
        }
      });
      return {
        ...that.page,
        ...applyForm
      };
    },
    //得到data
    getData() {
      this.$Loading.start();
      this.loading = true;
      return this.$http
        .post(this.$api.movedeposit_list, this.formatForm())
        .then(res => {
          this.$Loading.finish();
          if (res.data.result == "1" && res.data.data) {
            this.tableData = res.data.data;
            this.totalcount = +res.data.totalcount;
          } else {
            this.$Message.error(res.data.msg);
          }
        })
        .catch(err => {
          this.$Message.error("服务器异常");
        })
        .then(() => {
          this.loading = false;
        });
    },
    //切换页码
    indexChange(index) {
      this.page.pageindex = index - 1;
    },
    //切换每页显示数量
    sizeChange(size) {
      this.page.pagesize = size;
    },
    updateRow() {
      let that = this;
      if (that.updateForm.state == 1 && that.isConfirm == false) {
        that.isConfirm = true;
      } else {
        let params = {};
        if (that.updateForm.state == 1) {
          params = {
            id: that.updateForm.id,
            state: that.updateForm.state
          };
        } else {
          params = that.updateForm;
        }
        that.$http
          .post(that.$api.movedeposit_audit, params)
          .then(res => {
            if (res.data.result == "1") {
              that.$Message.success("更新成功!");
              that.getData();
            } else {
              that.$Message.error(res.data.msg);
            }
          })
          .catch(err => {
            that.$Message.error("服务器异常");
          })
          .then(() => {
            that.showConfirm = false;
          });
      }
    }
  }
};
</script>
<style scoped></style>



